  .burger-footer__icons {
    display: flex;
    gap: 16px;
    margin-bottom: 16px; }
  .burger-footer__mail {
    padding-bottom: 12px; }
  .burger-footer__phone {
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6px;
    font-weight: bold; }
  .burger-footer__langs {
    display: flex;
    gap: 7px;
    margin-top: 8px; }
  .burger-footer__lang {
    text-transform: uppercase;
    transition: 0.2s; }
    .burger-footer__lang:hover {
      transform: scale(1.1); }
    .burger-footer__lang--active {
      color: #7e7e82; }

@media screen and (min-width: 768px) {
  .burger-menu__container {
    width: 320px;
    padding-left: 24px;
    padding-right: 24px; }
  .burger-menu__header {
    margin-bottom: 25px; }
  .burger-nav__item {
    padding: 16px 0; }
    .burger-nav__item--active::before {
      left: -24px; }
  .burger-footer__langs {
    margin-top: 40px; } }

@media screen and (min-width: 1440px) {
  .burger-menu {
    display: block;
    position: static;
    z-index: 9; }
    .burger-menu__container {
      box-shadow: none;
      padding-top: 30px; }
    .burger-menu__header {
      height: auto; }
  .burger-nav__item {
    padding: 14px 0; }
    .burger-nav__item--active::before {
      height: 28px; } }

.show__burger-menu {
  display: block; }

.popup {
  display: none;
  position: fixed;
  z-index: 11;
  top: 0;
  right: 0; }
  .popup__container {
    min-width: 320px;
    width: 100vw;
    height: 100vh;
    padding: 24px 16px;
    display: flex;
    flex-direction: column;
    background-color: #ffffff;
    box-shadow: -16px 0 52px rgba(14, 24, 80, 0.2); }
  .popup__icon {
    margin-bottom: 32px; }
  .popup__title {
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6px;
    font-weight: bold;
    display: flex;
    align-items: center;
    position: relative;
    margin-bottom: 24px; }
    .popup__title::before {
      width: 4px;
      height: 40px;
      background-color: #41f6d7;
      left: -16px;
      border-radius: 0 4px 4px 0;
      position: absolute;
      content: ''; }

.form__input {
  border: 1px solid #eaeaea;
  border-radius: 6px;
  line-height: 24px;
  letter-spacing: -0.2px;
  padding: 12px 16px;
  width: 100%;
  margin-bottom: 16px;
  color: #1b1c21; }
  .form__input::-webkit-input-placeholder {
    color: #b5b6bc; }
  .form__input::-moz-placeholder {
    color: #b5b6bc; }
  .form__input:-ms-input-placeholder {
    color: #b5b6bc; }
  .form__input::placeholder {
    color: #b5b6bc; }

.form__text {
  margin-bottom: 18px; }

.form__btn {
  border-radius: 16px;
  background-color: #ff3e79;
  color: #ffffff;
  line-height: 24px;
  font-size: 12px;
  letter-spacing: -0.15px;
  font-weight: 500;
  text-transform: uppercase;
  padding: 4px 8px 4px 12px;
  display: flex;
  transition: 0.2s ease-in-out;
  margin-left: auto; }
  .form__btn::after {
    content: '';
    background: url(./static/arrow_more_white.svg);
    margin-left: 8px;
    width: 24px;
    height: 24px; }
  .form__btn:hover {
    box-shadow: 0 0 15px #ff3e79; }

.text {
  font-size: 14px;
  line-height: 16px;
  color: #7e7e82; }
  .text--pink {
    color: #ff3e79; }

@media screen and (min-width: 768px) {
  .popup__container {
    width: 320px;
    padding: 24px; }
  .popup__title::before {
    left: -24px; } }

@media screen and (min-width: 1440px) {
  .popup__container {
    width: 440px;
    padding: 32px; }
  .popup__icon {
    position: absolute;
    left: -72px;
    top: 32px; }
  .popup__title {
    margin-top: 4px; }
    .popup__title::before {
      left: -34px;
      border-radius: 4px; } }

.show__popup--call {
  display: block; }

.show__popup--feedback {
  display: block; }
